.header {
  background-color: $bg-header-dark;
  margin-bottom: 50px;
  padding: 8px 0px;
  .header__wrapper {
    padding: 5px 0px;
    width: 100%;
    margin: 0 auto;
    max-width: 1200px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    .header__column {
      i {
        position: relative;
        color: $red;
        font-size: 30px;
      }
      img {
        width: 120px;
        height: 25px;
      }
      &:nth-child(2) {
        width: 100%;
        justify-self: center;
      }
      &:last-child {
        justify-self: end;
      }
      ul {
        display: flex;
        color: white;
        font-weight: 600;
        text-transform: uppercase;
        li:not(:last-child) {
          margin-right: 15px;
        }
      }
      form {
        width: 100%;
        input {
          padding: 7px 10px;
          width: 100%;
          border-radius: 5px;
          font-size: 14px;
          color: rgba(255, 255, 255, 0.88);
          font-weight: 600;
          max-width: none;
          background-color: $bg-dark;
          &::placeholder {
            font-weight: 300;
            color: rgba(255, 255, 255, 0.88);
          }
        }
      }
    }
  }
}
